<!--  -->
<template>
	<el-row :gutter="20">
	   <el-col :span="12" :offset="6">
			<el-form ref="form" :rules="rules" :model="form" label-width="85px" @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
				<el-form-item label="" prop="userName">
					<el-input v-model="form.userName" style="width:98%" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="">
					 <el-select v-model="form.valueInstitutions" multiple placeholder="" style="width:98%">
					    <el-option
					      v-for="item in options"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					 </el-select>
				</el-form-item>

				<el-form-item label="" prop="gender">
					<el-select v-model="form.gender" placeholder="" style="width:200px;">
						 <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value" >
						 </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="2" prop="iconFile">
					<el-upload
					  class="avatar-uploader"
					  action="https://jsonplaceholder.typicode.com/posts/"
					  :show-file-list="false"
					  :on-success="handleAvatarSuccess"
					  :before-upload="beforeAvatarUpload"
					  v-model="form.photo">
					  <img v-if="imageUrl" :src="imageUrl" class="avatar">
					  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
					</el-upload>
				</el-form-item>
				<el-form-item label="" prop="phone">
					<el-input v-model="form.phone" style="width:98%" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="birthday">
					<div class="block">
					    <span class="demonstration"></span>
					    <el-date-picker
					      v-model="form.birthday"
					      type="date"
					      placeholder="">
					    </el-date-picker>
				    </div>
				</el-form-item>
				<el-form-item label="" prop="">
					 <el-select v-model="form.professional" multiple placeholder="" style="width:98%">
					    <el-option
					      v-for="item in options3"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					 </el-select>
				</el-form-item>
				<el-form-item label="">
					<el-checkbox-group v-model="form.checkList">
					    <el-checkbox label=""></el-checkbox>
					    <el-checkbox label=""></el-checkbox>
					    <el-checkbox label=""></el-checkbox>
					    <el-checkbox label="" ></el-checkbox>
					    <el-checkbox label="" ></el-checkbox>
					    <el-checkbox label="" ></el-checkbox>
					    <el-checkbox label="" ></el-checkbox>
				  	</el-checkbox-group>
				</el-form-item>
				<el-form-item label="">
					<div class="block">
					    <span class="demonstration"></span>
					    <el-date-picker
					      v-model="value1"
					      type="date"
					      placeholder="">
					    </el-date-picker>
				    </div>
				</el-form-item>
				<el-form-item label="" >
					<el-select v-model="form.region" placeholder="" style="width:200px;">
						 <el-option v-for="item in options4" :key="item.value" :label="item.label" :value="item.value" >
						 </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" >
					<el-select v-model="form.region" placeholder="" style="width:200px;">
						 <el-option v-for="item in options5" :key="item.value" :label="item.label" :value="item.value" >
						 </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" >


				</el-form-item>

				
				<el-form-item label="">
					<el-input type="textarea" v-model="form.desc" style="width:98%;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="onSubmit"></el-button>
					<el-button plain></el-button>
				</el-form-item>
			</el-form>
		</el-col>
	</el-row>
</template>

<script>
  import {getOrgEmployeeAddCoach} from '../../../api/api';

	export default {
		data() {
			return {
				checkList: [],
				 pickerOptions1: {
			          disabledDate(time) {
			            return time.getTime() > Date.now();
			          },
			          shortcuts: [{
			            text: '',
			            onClick(picker) {
			              picker.$emit('pick', new Date());
			            }
			          }, {
			            text: '',
			            onClick(picker) {
			              const date = new Date();
			              date.setTime(date.getTime() - 3600 * 1000 * 24);
			              picker.$emit('pick', date);
			            }
			          }, {
			            text: '',
			            onClick(picker) {
			              const date = new Date();
			              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
			              picker.$emit('pick', date);
			            }
			          }]
			        },
			        value1: '',
				 options: [{
	                value: '1',
	                label: ''
	              }, {
	                value: '2',
	                label: ''
	              }],
	              value: '',
	            options2: [{
	                value: '1',
	                label: ''
	              }, {
	                value: '2',
	                label: ''
	              }],
	              value5: [],
	            options3: [{
	                value: '1',
	                label: ''
	              }, {
	                value: '2',
	                label: ''
	              }],
	              value5: [],
	             options4: [{
			          value: '1',
			          label: ''
			        }, {
			          value: '2',
			          label: ''
			        }, {
			          value: '3',
			          label: ''
			        }, {
			          value: '4',
			          label: ''
			        }, {
			          value: '5',
			          label: ''
			        },{
			          value: '6',
			          label: ''
			        },{
			          value: '7',
			          label: ''
			        }],
	              value: '',
	             options5: [{
	                value: '1',
	                label: ''
	              }, {
	                value: '2',
	                label: ''
	              }],
	              value: '',
				form: {
					userName: '',
					valueInstitutions:'',
					gender: '',
					photo:'',
					phone:'',
					birthday:'',
					professional:'',
					checkList:'',
					delivery: false,
					type: [],
					resource: '',
					desc: '',
					imageUrl: '',
					num1:1,
				},
				rules:{
					userName:[
						{required: true,message:'',trigger: 'blur'},
					],
					valueInstitutions:[
						{type:'number',required: true,message:'',trigger: 'change'},
					],
					gender:[
						{type:'text',required: true,message:'',trigger: 'change'},
					],
					
				},
				 dialogImageUrl: '',
        		 dialogVisible: false
			}
		},
		methods: {
			 onSubmit:function() {
                getOrgEmployeeAddCoach({userName:this.form.userName,valueInstitutions:this.form.valueInstitutions,
                    gender:this.form.gender,phone:this.form.phone,}).then(resp=>{
                    if (resp.resultCode == 0) {
                        this.$message({
                            message:"",
                            type: "success"
                        });
                        this.showTableView();
                    } else {
                        this.$message({
                            message:resp.message,
                            type: 'error'
                        });
                    }
                });
			  },
            back:function () {
                this.showTableView();
            },
			 handleRemove(file, fileList) {
		        console.log(file, fileList);
		      },
		      handlePictureCardPreview(file) {
		        this.dialogImageUrl = file.url;
		        this.dialogVisible = true;
		      },
			
			 handleAvatarSuccess(res, file) {
		        this.imageUrl = URL.createObjectURL(file.raw);
		      },
		      beforeAvatarUpload(file) {
		        const isJPG = file.type === 'image/jpeg';
		        const isLt2M = file.size / 1024 / 1024 < 2;

		        if (!isJPG) {
		          this.$message.error(' JPG !');
		        }
		        if (!isLt2M) {
		          this.$message.error(' 2MB!');
		        }
		        return isJPG && isLt2M;
		      },
		       handleChange(value) {
		        console.log(value);
		      },
		      // NewCoachSubmit(formName){
		      // 	 this.$refs[formName].validate(valide=>{
		      // 	 	if(valide){
		      // 	 		console.log('')
		      // 	 	}else{
		      // 	 		console.log('')
		      // 	 	}
		      // 	 })
		      // }
		    },

		}
	

</script>
<style>
	 .avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	  }
	  .avatar-uploader .el-upload:hover {
	    border-color: #409EFF;
	  }
	  .avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 178px;
	    height: 178px;
	    line-height: 178px;
	    text-align: center;
	  }
	  .avatar {
	    width: 178px;
	    height: 178px;
	    display: block;
	  }
</style>


