export function setAuthorization(authorization){
    axios.defaults.headers.common['Authorization'] = authorization ;//#sessionStorage.getItem("authorization")
    console.log("authorization");
}



/**
 * get
 * @param url
 * @param data
 * @returns {Promise}
 */

export function get(url,params={}){
  return new Promise((resolve,reject) => {
    axios.get(url,{
      params:params
    })
    .then(response => {
      resolve(response.data);
    })
    .catch(err => {
      reject(err)
    })
  })
}


/**
 * post
 * @param url
 * @param data
 * @returns {Promise}
 */

 export function post(url,data = {},config={}){
    return new Promise((resolve,reject) => {
        axios.post(url,data,config)
            .then(response => {
                // console.log(response);
                resolve(response.data);
            },err => {
                reject(err)
            }).catch(err => {
            reject(err)
        });
    })
}
export function postResp(url,data = {}){
    return new Promise((resolve,reject) => {
        axios.post(url,data)
            .then(response => {
                // console.log(response);
                resolve(response);
            },err => {
                reject(err)
            }).catch(err => {
            reject(err)
        });
    })
}

 /**
 * patch
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patch(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.patch(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
               reject(err)
           }).catch(err => {
               reject(err)
           });
  })
}

 /**
 * put
 * @param url
 * @param data
 * @returns {Promise}
 */

export function put(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.put(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
               reject(err)
           }).catch(err => {
               reject(err)
           });
  })
}

export default {
    put,
    patch,
    get,
    post,
    postResp,
    setAuthorization
}



