      //   .then(function(response){
      //     alert(response.data);
      //   }).bind(this)
      //   .catch(function(error){
      //     console.log(error)
      //   })
      // },

      changeMsg(){
        this.isVisible=false;
      },
      rowClass({row,rowIndex}){
        console.log(0)
        return 'background:#f1f2f7'
      },
      

      selsChange: function (sels) {
        this.sels = sels;
      },
       //
        handleSizeChange: function(val) {
            this.pageSize = val;
            this.loadData( this.currentPage, this.pageSize);
        },
        //
        handleCurrentChange: function(val) {
            this.currentPage = val;
            this.loadData( this.currentPage, this.pageSize);
        },
         getState:function(){
            getState().then(resp=>{
                this.states=resp.data.data;
            });
        },
       searchData:function(){
            this.loadData( this.currentPage, this.pageSize);
        },
        loadData: function(currentPage, pageNum){
            var _this = this ;
            CourseDelete( qs.stringify({currentPage:currentPage,pageNum:pageNum,state:this.state,
            })).then(resp=>{
                // _this.roles = ;
                _this.users = resp.data.data;
                _this.totalCount =resp.data.totalCount;
            });
        },
        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
      //
       deleteSelection(){
            this.$confirm('', '', {
                distinguishCancelAndClose: true,
                confirmButtonText: '',
                cancelButtonText: ''
            }).then(() => {
                let ids = [];
                const params = new URLSearchParams();
                this.multipleSelection.forEach(item=>{
                    ids.push(item['roleId'])
                    params.append("ids",item['roleId']);
                })
               

                getOrgDelete(ids,{isArray:true, arrayId:"ids"}).then(resp=>{
                    if (resp.resultCode == 0) {
                        this.$message.success("");
                        this.searchData();
                    } else {
                        this.$message.error("");
                    }
                });
            });

        },

    },
    mounted() {
       
       this.getState();

      var _this = this ;  //
      getOrgList({}).then(resp=>{
        console.log(resp);
        _this.users = resp.data.data;
      },err=>{
        console.log(err);
      }).catch(err=>{
        console.log(err);
      })
    
    }
  }

</script>

