				<el-form-item label="" >
					<el-input v-model="form.name" style="width:140px;padding-right:10px;"></el-input>
					<el-select v-model="form.region" placeholder="" style="width:200px;">
						<el-option label="" value="shanghai"></el-option>
						<el-option label="" value="beijing"></el-option>
					</el-select>
					<el-input v-model="form.name" style="width:140px;padding-left:10px;"></el-input>
					<el-input v-model="form.name" style="width:340px;padding-right:10px;padding-top:20px;"></el-input>
					<el-input v-model="form.name" style="width:155px"></el-input>
				</el-form-item>
				<!-- <el-form-item label="">
					<el-col :span="11" style="padding-left:0px;">
						<el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
					</el-col>
					<el-col class="line" :span="2">-</el-col>
					<el-col :span="11">
