              <el-table-column  label="" width="100"  prop="level">
              </el-table-column>


              <el-table-column  label="" width="200" prop="parentDepartmentName">
              </el-table-column>
              <el-table-column prop="" label="" width="120" prop="state">
              </el-table-column>
              

              <el-table-column label="" align="center" width="250" class="el_tep" style="padding-right:15px;">
                <template scope="scope" >
                  <span style="cursor:pointer;"></span>
                  <span class="el_tep_d"></span>
                  <span style="cursor:pointer;"></span>
                  <span class="el_tep_d"></span>
