import OrgLogin from './views/OrgLogin.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Org from './Org.vue'
import RegisteredUsers from './views/UserManagement/RegisteredUsers.vue' //
import BusinessManagementB from './views/UserManagement/BusinessManagementB.vue' //(B)
import PurchaseRecordsB from './views/UserManagement/forms/PurchaseRecordsB.vue' //-(B)
import InvoiceInformationB from './views/UserManagement/forms/InvoiceInformationB.vue' //-(B)
import ReceivesAnAddressB from './views/UserManagement/forms/ReceivesAnAddressB.vue' //-(B)
import BusinessManagementP from './views/UserManagement/BusinessManagementP.vue' //(P)
import ChildAccount from './views/UserManagement/forms/ChildAccount.vue' //(P)
import PurchaseRecordsP from './views/UserManagement/forms/PurchaseRecordsP.vue' //-(P)
import InvoiceInformationP from './views/UserManagement/forms/InvoiceInformationP.vue' //-(P)
import ReceivesAnAddressP from './views/UserManagement/forms/ReceivesAnAddressP.vue' //-(P)
import NewSubAccountP from './views/UserManagement/forms/NEW/NewSubAccountP.vue' //(P)

import AgencyInformation from './views/OrganizationManagement/AgencyInformation.vue' //
import TheCoachInformation from './views/OrganizationManagement/TheCoachInformation.vue' //
import CourseInformation from './views/OrganizationManagement/CourseInformation.vue' //
import StudentInformation from './views/OrganizationManagement/StudentInformation.vue'  //
import ClassInformation from './views/OrganizationManagement/ClassInformation.vue'  //
import SiteManagement from './views/OrganizationManagement/SiteManagement.vue'  //
import ArrangingRules from './views/CoursesArrangingManagement/ArrangingRules.vue'  //
import CourseArranging from './views/CoursesArrangingManagement/CourseArranging.vue'  //
import ManualArranging from './views/CoursesArrangingManagement/ManualArranging.vue'  //
import OrderList from './views/OrderCenter/OrderList.vue'  //
import RefundOrders from './views/OrderCenter/RefundOrders.vue'  //
import OrderKanban from './views/OrderCenter/OrderKanban.vue'  //
import PromotionRules from './views/SalesManagement/PromotionRules.vue'  //
import ClientReview from './views/After-salesService/ClientReview.vue'  //
import SatisfactionSurvey from './views/After-salesService/SatisfactionSurvey.vue'  //
import OrganizationalStructure from './views/SystemManagement/OrganizationalStructure.vue'  //
import StaffManagement from './views/SystemManagement/StaffManagement.vue'  //
import RoleManagement from './views/SystemManagement/RoleManagement.vue'  //
import SystemParameterConfiguration from './views/SystemManagement/SystemParameterConfiguration.vue'  //
import DataDictionary from './views/SystemManagement/DataDictionary.vue'   //
import EditSubAccount from './views/UserManagement/forms/EditSubAccount.vue' //
import NewSubAccount from './views/UserManagement/forms/NewSubAccount.vue' //
import EditMerchants from './views/UserManagement/forms/EditMerchants.vue'//
import NewBusiness from './views/UserManagement/forms/NewBusiness.vue' //
import UserAgreement from './views/LoginTo/UserAgreement.vue'
//import UserAgreement from './views/charts/UserAgreement.vue'
import register from './views/charts/register.vue'//
import RetrievePassword from './views/charts/RetrievePassword.vue'//
import PersonalData from './views/charts/PersonalData.vue'//
import ChangePassword from './views/charts/ChangePassword.vue'//
import NewInstitutions from './views/OrganizationManagement/NewlyAdded/NewInstitutions.vue' //
import CoachInformation from './views/OrganizationManagement/NewlyAdded/CoachInformation.vue' //
import NewTrainer from './views/OrganizationManagement/NewlyAdded/NewTrainer.vue' //
import NewCourses from './views/OrganizationManagement/NewlyAdded/NewCourses.vue' //  New site
import NewSite from './views/OrganizationManagement/NewlyAdded/NewSite.vue'

import InvoiceInformation from './views/charts/InvoiceInformation.vue'
// import PlatformLogin from './views/PlatformLogin.vue'
import AddRole from './views/SystemManagement/add/AddRole'

import echarts from './views/charts/echarts.vue'
import Forms1 from './views/charts/Forms1.vue'
import Forms3 from './views/charts/Forms3.vue'
import Forms4 from './views/charts/Forms4.vue'


let orgRoutes = [
    {
        path: '/orgLogin',
        component: OrgLogin,
        name: 'OrgLogin',
        hidden: true,
        // childern:[
        //     {path: '/UserAgreement', component: UserAgreement, name: 'UserAgreement', hidden: false }
        // ]
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/user-agreement',
        component: UserAgreement,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        hidden: true,
        children:[
            { path: '/forms1', component: Forms1, name: 'Forms1', hidden: false },
            { path: '/forms3', component: Forms3, name: 'Forms3', hidden: false },
            { path: '/forms4', component: Forms4, name: 'Forms4', hidden: false },
            /*{ path: '/UserAgreement', component: UserAgreement, name: 'UserAgreement', hidden: false },*/
            { path: '/register', component: register, name: 'register', hidden: false },
            { path: '/RetrievePassword', component: RetrievePassword, name: 'RetrievePassword', hidden: false },
            { path: '/PersonalData', component: PersonalData, name: 'PersonalData', hidden: false },
            { path: '/ChangePassword', component: ChangePassword, name: 'ChangePassword', hidden: false },
            { path: '/InvoiceInformation', component: InvoiceInformation, name: 'InvoiceInformation', hidden: false },
        ]
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/org', component: Org, name: '', hidden: true },
            { path: '/RegisteredUsers', component: RegisteredUsers, name: ''},
            { path: '/BusinessManagementB', component: BusinessManagementB, name: '(B)',
                 children: 
                    [
                        {path:'EditSubAccount', component:EditSubAccount, name:''},
                        {path:'NewSubAccount', component:NewSubAccount, name:''},
                        {path:'PurchaseRecordsB', component:PurchaseRecordsB, name:''},
                        {path:'InvoiceInformationB', component:InvoiceInformationB, name:''},
                        {path:'ReceivesAnAddressB', component:ReceivesAnAddressB, name:''}

                ]

        },
            { path: '/BusinessManagementP', component: BusinessManagementP, name: '(P)',
                 children: 
                    [
                        {path:'EditMerchants', component:EditMerchants, name:''},
                        {path:'NewBusiness', component:NewBusiness, name:''},
                        {path:'ChildAccount', component:ChildAccount, name:'',
                            children:
                           [
                                {path:'NewSubAccountP', component:NewSubAccountP, name:'(P)'}
                           ] 
                    },
                        {path:'PurchaseRecordsP', component:PurchaseRecordsP, name:''},
                        {path:'InvoiceInformationP', component:InvoiceInformationP, name:''},
                        {path:'ReceivesAnAddressP', component:ReceivesAnAddressP, name:''}
                ]

            },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/AgencyInformation', component: AgencyInformation, name: '' ,
                 children: 
                    [
                       {path:'NewInstitutions', component:NewInstitutions, name:''},
                       {path:'CoachInformation', component:CoachInformation, name:'()'},
                ],

        },
            { path: '/TheCoachInformation', component:TheCoachInformation, name: '' ,
                 children:
                     [
                       {path:'NewTrainer', component:NewTrainer, name:''},
                ],
        },
            { path: '/CourseInformation', component: CourseInformation, name: '' ,
                 children:
                     [
                       {path:'NewCourses', component:NewCourses, name:''},
                ],

        },
            { path: '/StudentInformation', component: StudentInformation, name: '' },
            { path: '/ClassInformation', component: ClassInformation, name: '' , 
                 children:
                     [
                       {path:'NewSite', component:NewSite, name:''},
                ],
        },
            { path: '/SiteManagement', component: SiteManagement, name: '' },
        ]
    },
    // {
    //     path: '/',
    //     component: Home,
    //     name: '',
    //     iconCls: 'fa fa-address-card',
    //     leaf: true,//
    //     children: [
    //         { path: '/page6', component: Page6, name: '' }
    //     ]
    // },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/ArrangingRules', component: ArrangingRules, name: '' },
            { path: '/CourseArranging', component: CourseArranging, name: '' },
            { path: '/ManualArranging', component: ManualArranging, name: '' },
            
            //{ path: '/echarts', component: echarts, name: 'echarts' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/OrderList', component: OrderList, name: '' },
            { path: '/RefundOrders', component: RefundOrders, name: '' },
            { path: '/OrderKanban', component: OrderKanban, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/PromotionRules', component: PromotionRules, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/ClientReview', component: ClientReview, name: '' },
            { path: '/SatisfactionSurvey', component:SatisfactionSurvey, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/OrganizationalStructure', component: OrganizationalStructure, name: '' },
            { path: '/StaffManagement', component: StaffManagement, name: '' },
            { path: '/RoleManagement', component: RoleManagement, name: '',
                children:
                    [
                        {path:'addRole', component:AddRole, name:''},
                    ],
            },
            { path: '/SystemParameterConfiguration', component: SystemParameterConfiguration, name: '' },
            { path: '/DataDictionary', component: DataDictionary, name: '' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default orgRoutes;


